import React, { Component } from 'react'
export default class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      active: 0,
      navList: [
        { name: 'Popular', path: '/' },
        { name: 'Battle', path: '/battle' },
      ],
    }
  }
  componentDidMount() {
    let { props } = this.props
    let { pathname } = props.location
    let idx = this.state.navList.findIndex((el) => el.path === pathname)
    this.checkPage(idx)
  }
  render() {
    const { active, navList } = this.state
    return (
      <div className="nav-page">
        {navList.map((opt, i) => (
          <span key={opt.name} className={Number(active) === i ? 'active' : ''} onClick={() => this.checkPage(i)}>
            {opt.name}
          </span>
        ))}
      </div>
    )
  }
  checkPage = (i) => {
    const { props } = this.props
    this.setState({ active: i })
    let param = props.location.search ? new URLSearchParams(props.history.location.search).get('type') : ''
    let player1 = props.location.search ? new URLSearchParams(props.history.location.search).get('player1') : ''
    let player2 = props.location.search ? new URLSearchParams(props.history.location.search).get('player2') : ''
    if (i === 0) {
      props.history.push(`/${param ? `?type=${param}` : ''}`)
    } else if (i === 1 || !player1 || !player2) {
      this.setState({ active: 1 })
      props.history.push('/battle')
    }
  }
}
