import { lazy } from 'react'

export default [
  {
    path: '/',
    component: lazy(() => import('../page/home.js')),
    isExact: true,
    meta:{
      title: '首页'
    }
  },
  {
    path: '/battle',
    component: lazy(() => import('../page/battle.js')),
    isExact: true,
    meta:{
      title: '对抗'
    }
  },
  {
    path: '/battle/result',
    component: lazy(() => import('../page/result.js')),
    isExact: true,
    meta:{
      title: '对抗结果'
    }
  }
]